import React from 'react';
import styles from './Projects.module.css';

class Projects extends React.Component{
    constructor(props){
        super(props);
    }

    render(){
        return(
            <div id={styles.projectsOuter}>
                <div id={styles.neuroTitle}>Fully Dynamic Campus Involvement Tool</div>
                <div id={styles.neuroDescription}>As part of a startup conceived by colleagues at my university, we developed a fully dynamic and immersive
                tool for those new to campus to be able to easily get involved with student clubs and organizations. Besides just a list of available organizations, this app includes
                a personalized quiz and subsequent club recommender system for each student, as well as tag-based searching and the ability to apply to clubs directly through the app. 
                Club officers can also create applications and view and filter candidates as well as create events and put it on the club calendar all through the app.</div>
                <div id={styles.neuroGit}>Company Website: https://www.uniflowcampus.com</div>

                <div id={styles.colorTitle}>"Color Hop" 2D Platformer Game</div>
                <div id={styles.colorDescription}>I built a 2D platformer game using Unity Game Engine and many C# scripts in which the player has to hop on 
                a course of dissapearing black beams that change color in order to get to the top under a time limit. Through this project I learned the importance
                of well-thought-out game mechanics as well as how physics and vector graphics intersects with computer programming. I am working on embedding the game onto this site.</div>
                <div id={styles.colorGit}>Github Repo Link: https://github.com/srikar7a/ColorHop</div>
                
            </div>
        );
    }
};

export default Projects;